<template>
  <section>
    <div v-for="(data, index, param_key) in filterShowData.slice(0, 1)" :key="param_key">
      <div class="banner">
        <div class="container">
          <div class="detail_img" :style="{ 'background-image': 'url(' + data.imageUrl + ')' }"></div>
        </div>
      </div>
      <div class="detail_main">
        <div class="container mt-5 mb-5">
          <div class="row">
            <div class="col-12">
              <div class="main">
                  <h2>《{{data.title}}》</h2>
              </div>
            </div>
            <div class="col-12">
              <div class="main">
                <div class="main_title">
                  <h2>簡介：</h2>
                  <br>
                  <p>地點：{{data.showInfo[0].location}}</p>
                  <p>地點名稱：{{data.showInfo[0].locationName}}</p>
                  <p v-show="data.showInfo[0].price !=''">票價：{{data.showInfo[0].price}}</p>
                  <p v-show="data.sourceWebPromote !=''">
                    活動網址：
                    <a
                      :href="data.sourceWebPromote"
                      style="color:rgb(138, 205, 221);"
                    >{{data.sourceWebPromote}}</a>
                  </p>
                  <hr>
                  <p>{{data.descriptionFilterHtml}}</p>
                </div>
              </div>
            </div>
            <div class="col-12">
              <div class="main">
                <div class="main_title">
                  <h2>地圖</h2>
                  <br>
                  <!-- {{data.showInfo[0].location}} -->
                  <iframe
                    :src="'https://www.google.com/maps?q=' + data.showInfo[0].location + '=&hl=cn&output=embed'"
                    height="400"
                    frameborder="0"
                    style="border:0;width:100%;"
                    allowfullscreen
                  ></iframe>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  name: "paramspage",
  props: {
    showData: {
      type: Array,
      required: true
    }
  },
  computed: {
    filterShowData() {
      let paramTitle = new RegExp(this.$route.params.title, "g");

      return this.showData.filter(el => {
        return el.title.search(paramTitle) != -1;
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.detail_img{
  background-size: contain;
}
</style>
